import * as mainConstanst from '../reducers/constant'
import axios from "axios";
const mockApiData = require('./../data');

export const getBook = () => async dispatch => {
  dispatch({type: mainConstanst.FETCH_Books_START});
  try {
    const foods = await axios.get("http://www.Bookpuppy.com/api/?i=onions,garlic&q=salat&p=1",{headers: {'Content-Type': 'text/plain'}});
    const localUsers = JSON.parse(localStorage.getItem("Books"));
    if (!localStorage.getItem("Books")) {
      dispatch({
        type: mainConstanst.FETCH_Books_SUCCESS,
        Books: foods.data.results
      });
    } else {
      dispatch({
        type: mainConstanst.FETCH_LOCAL_BookS_SUCCESS,
        Books: localUsers
      });
    }
  }
  catch (err) {
    const localUsers = JSON.parse(localStorage.getItem("Books"));
    if (!localStorage.getItem("Books")) {
      dispatch({
        type: mainConstanst.FETCH_Books_SUCCESS,
        Books: mockApiData.results
      });
    } else {
      dispatch({
        type: mainConstanst.FETCH_LOCAL_BookS_SUCCESS,
        Books: localUsers
      });
    }

  }
};
export const searchBook = value => {
  return {
    type: mainConstanst.SEARCH_Book,
    value
  };
};
export const removeBook = Book => {
  return {
    type: mainConstanst.REMOVE_MY_Book,
    Book
  };
};
export const addBook = Book => {
  return {
    type: mainConstanst.ADD_MY_Book,
    Book
  };
};




