import React, { Fragment } from "react";
import { ListGroup, ListGroupItem, Button } from "reactstrap";

import Loader from "../Loader/Loader";


const myBookList = props => {
  const {Books, onRemovemyBook, loaded, onAddToFavemyBook, onRemoveToFavemyBook} = props;
  console.log('myBook-list',Books);
  return (
    <Fragment>
      {loaded ? (
        <div className="myBook-list">
          <p>{Books.length === 1 ? `You have ${Books.length} myBook` : Books.length > 1 ? `You have ${Books.length} myBooks` : "No have yours Books"}</p>
          <ListGroup>
            {Books.map((myBook, index) =>
              <ListGroupItem
                key={index}>
                {myBook.thumbnail ?
                  <img
                    src={myBook.thumbnail}
                    alt="Book-pic"/>
                  : <img
                    src={"http://img.Bookpuppy.com/560551.jpg"}
                    alt="Book-pic"/>}
                <span>
                {myBook.title}
                </span>
                <Button
                  color="danger"
                  onClick={() => onRemovemyBook(myBook)}>
                  Remove
                </Button>

              </ListGroupItem>
            )}
          </ListGroup>
        </div>
      ) : <Loader />}
    </Fragment>
  );
};

export default myBookList;


