import React, {Fragment} from "react";
import {Button, ListGroup, ListGroupItem, Input} from 'reactstrap';

import Loader from '../Loader/Loader';
// import Pagination from "../Pagination/index";
import {Link} from "react-router-dom";
import './BookList.scss'

const BookList = props => {
  const {value,onRemoveBook, onAddBook, Books, currentPage, itemsPerPage, onInputChange, page, loaded} = props;
  const startOffset = (currentPage - 1) * itemsPerPage;
  let startCount = 0;

  return (
    <Fragment>
      {loaded ? (
        <div className="search-list">
          <p>last {Books.length} Book</p>
          <Input
            type="text"
            placeholder="Please, enter name of Books or ingredients"
            value={value}
            onChange={({target}) => onInputChange(target.value)}
          />
          <ListGroup>
            {Books.map((Book, index) => {
              return index >= startOffset && startCount < itemsPerPage ? ++startCount && (
                <ListGroupItem
                  key={index}
                  className="text-center">
                  {Book.thumbnail ?
                    <img
                      src={Book.thumbnail}
                      alt="Book-pic"/>
                    : <img
                      src={"http://img.Bookpuppy.com/560551.jpg"}
                      alt="Book-pic"/>}
                  <p>
                    {Book.title}
                  </p>
                  <h3>ingredients:</h3>
                  <p>{Book.ingredients}</p>
                  {!Book.isBook ? (
                    <Button
                      style={{marginBottom: "10px"}}
                      color="success"
                      onClick={() => onAddBook(Book)}>
                      Add To List
                    </Button>
                  ) : (
                    <Button
                      style={{marginBottom: "10px"}}
                      color="danger"
                      onClick={() => onRemoveBook(Book)}>
                      Remove From Friend List
                    </Button>
                  )}
                </ListGroupItem>
              ) : (
                null
              );
            })}
          </ListGroup>

        </div>
      ) : <Loader/>}
    </Fragment>
  );
};

export default BookList;


